@import '../../base';

$whyPartnerPadingTop: 130px;
$whyPartnerPadingBottom: 30px;
$whyPartnerTextMaxWidth: 350px;

.partnership-container {
  .why-partner {
    padding-top: $whyPartnerPadingTop;
    padding-bottom: $whyPartnerPadingBottom;

    @media screen and (max-width: 767px) {
      padding-top: ($whyPartnerPadingTop - 80px);
    }

    .title {
      max-width: $whyPartnerTextMaxWidth;

      @media screen and (max-width: 767px) {
        max-width: unset;
      }
    }

    .desc {
      max-width: $whyPartnerTextMaxWidth;
      font-size: 14px;
      margin-bottom: 25px;
      color: $color-input-form-landing-desc2;

      @media screen and (max-width: 767px) {
        max-width: unset;
      }
    }

    .image {
      max-width: 100%;
    }
  }

  .features {
    .feature {
      box-shadow: 0px 30px 20px rgba(0, 0, 0, 0.03);
      background-color: #fff;
      border-radius: 20px;
      padding: 30px;

      @media screen and (max-width: 767px) {
        margin-bottom: 30px;
      }

      .image {
        max-width: 100%;
        height: 50px;
        display: block;
        margin: 0 auto;
      }

      .title {
        text-align: center;
        display: block;
        margin: 30px auto 20px auto;
        font-weight: bold;
        font-size: 18px;
        color: $color-input-text;
      }

      .desc {
        text-align: center;
        display: block;
        margin: 0 auto 30px auto;
        font-weight: 600;
        font-size: 14px;
        max-width: 100%;
        width: 400px;
        color: $color-input-form-landing-desc2;
      }

      .button {
        display: block;
        margin: 0 auto;
      }
    }
  }
}